.carCard {
  width: 274px;
  margin-bottom: 20px;
}

.carImgContainer {
  width: 274px;
  height: 268px;
}

.imgThumb {
  position: relative;
}

.carImg {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 14px;
}

.deleteFavoriteBtn {
  position: absolute;
  top: 5px;
  right: 5px;
  color: rgba(52, 112, 255, 1);
  background-color: transparent;
  border: none;
}

.addFavoriteBtn {
  position: absolute;
  top: 5px;
  right: 5px;
  color: rgba(255, 255, 255, 0.8);
  background-color: transparent;
  border: none;
}

.cardTitleWrap {
  width: 274px;
  display: flex;
  justify-content: space-between;
  margin-top: 14px;
  margin-bottom: 8px;
}

.cardTitleSpan {
  color: rgba(52, 112, 255, 1);
}

.baseInfCard {
  font-size: 12px;
  line-height: 18px;
  color: rgba(18, 20, 23, 0.5);
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  margin-bottom: 4px;
}

.baseInfCard li {
  position: relative;
  padding-right: 6px;
}

.baseInfCard li::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 1px;
  background-color: rgba(18, 20, 23, 0.1);
}

.baseInfCard li:last-child::after {
  content: none;
}

.learnMoreBtn {
  margin-top: 24px;
  padding: 12px 102px;
  color: rgba(255, 255, 255, 1);
  background-color: rgba(52, 112, 255, 1);
  border: none;
  border-radius: 12px;
}

.learnMoreBtn:hover,
.learnMoreBtn:focus {
  background-color: rgba(11, 68, 205, 1);
}
